import React, { Component } from "react";
import "../../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import TrainerJockeyFormLines from "../trainerjockeyformlines/TrainerJockeyFormLines";
import ReportSelection from "../selectors/ReportSelection";

class TrainerJockeyForm extends Component {
  onUpdate = selection => {
    // want to access the TrainerJockeyFormLines component
    // to filter the results
    this.refs.output.filterForm(selection);
  };

  render() {
    return (
      <div>
        <div className="container">
          <h2 className="mb-3">Trainer Jockey Form</h2>
        </div>
        <ReportSelection onUpdate={this.onUpdate} />
        <TrainerJockeyFormLines ref="output" />
      </div>
    );
  }
}

export default TrainerJockeyForm;
