import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Header from "./components/layout/Header";
import TrainerJockeyForm from "./components/pages/TrainerJockeyForm";
import About from "./components/pages/About";

class App extends Component {
  render() {
    return (
      <Router basename={process.env.PUBLIC_URL || ""}>
        <Switch>
          <div className="App">
            <Header branding="Mal's Racing Info" />
            {/* <TrainerJockeyForm /> */}
            <Route
              exact
              path="/trainerjockeyform"
              component={TrainerJockeyForm}
            />
            <Route path="/trainerjockeyform/about" component={About} />
          </div>
        </Switch>
      </Router>
    );
  }
}

export default App;
