import React, { Component } from "react";
import Axios from "axios";

class TrainerJockeyFormLines extends Component {
  state = {
    orgFormLines: [],
    formLines: []
  };

  componentDidMount() {
    Axios.get(
      /* "http://localhost:55645/api/TrainerJockeyFormLines/Get14DayForm" */
      "https://malstissue.azurewebsites.net/api/TrainerJockeyFormLines/Get14DayForm"
    ).then(res =>
      this.setState({
        orgFormLines: res.data,
        formLines: res.data
      })
    );
  }

  filterForm = filter => {
    const { orgFormLines } = this.state;
    let newFormLines = orgFormLines;

    if (!isNaN(filter.runs) && filter.runs !== "" && filter.runs > 0)
      newFormLines = newFormLines.filter(
        formline => formline.Runs >= filter.runs
      );

    if (!isNaN(filter.wins) && filter.wins !== "" && filter.wins > 0)
      newFormLines = newFormLines.filter(
        formline => formline.Wins >= filter.wins
      );

    if (!isNaN(filter.placed) && filter.placed !== "" && filter.placed > 0)
      newFormLines = newFormLines.filter(
        formline => formline.Placed >= filter.placed
      );

    if (
      !isNaN(filter.winPercent) &&
      filter.winPercent !== "" &&
      filter.winPercent > 0
    )
      newFormLines = newFormLines.filter(
        formline => formline.WinPercent >= filter.winPercent
      );

    if (
      !isNaN(filter.placedPercent) &&
      filter.placedPercent !== "" &&
      filter.placedPercent > 0
    )
      newFormLines = newFormLines.filter(
        formline => formline.PlacedPercent >= filter.placedPercent
      );

    if (
      !isNaN(filter.winProfitLoss) &&
      filter.winProfitLoss !== "" &&
      filter.winProfitLoss > -1000
    )
      newFormLines = newFormLines.filter(
        formline => formline.WinProfitLoss >= filter.winProfitLoss
      );

    if (
      !isNaN(filter.placedProfitLoss) &&
      filter.placedProfitLoss !== "" &&
      filter.placedProfitLoss > -1000
    )
      newFormLines = newFormLines.filter(
        formline => formline.PlacedProfitLoss >= filter.placedProfitLoss
      );

    if (!isNaN(filter.AE) && filter.AE !== "" && filter.AE > 0)
      newFormLines = newFormLines.filter(formline => formline.AE >= filter.AE);

    if (!isNaN(filter.IV) && filter.IV !== "" && filter.IV > 0)
      newFormLines = newFormLines.filter(formline => formline.IV >= filter.IV);

    this.setState({
      formLines: newFormLines
    });
  };

  render() {
    const { formLines } = this.state;
    console.log(`render ${formLines.length}`);
    return (
      <div>
        <div className="container">
          <table
            className="table table-striped table-sm table-bordered"
            style={{
              fontFamily: "Open Sans",
              fontSize: "0.95em"
            }}
          >
            <thead>
              <tr>
                <th>Trainer</th>
                <th>Jockey</th>
                <th>Runs</th>
                <th>Wins</th>
                <th>Places</th>
                <th>Win%</th>
                <th>Place%</th>
                <th>Win P/L</th>
                <th>EW P/L</th>
                <th>A/E</th>
                <th>IV</th>
              </tr>
            </thead>
            <tbody>
              {formLines.map(formLine => (
                <tr>
                  <td>{formLine.Trainer}</td>
                  <td>{formLine.Jockey}</td>
                  <td>{formLine.Runs}</td>
                  <td>{formLine.Wins}</td>
                  <td>{formLine.Placed}</td>
                  <td>{formLine.WinPercent}</td>
                  <td>{formLine.PlacedPercent}</td>
                  <td>{formLine.WinProfitLoss}</td>
                  <td>{formLine.PlacedProfitLoss}</td>
                  <td>{formLine.AE}</td>
                  <td>{formLine.IV}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default TrainerJockeyFormLines;
