import React, { Component } from "react";
import RunSelector from "./RunSelector";
import PercentSelector from "./PercentSelector";
import ProfitLossSelector from "./ProfitLossSelector";
import AeIvSelector from "./AeIvSelector";

class ReportSelection extends Component {
  state = {
    runs: "",
    wins: "",
    placed: "",
    winPercent: "",
    placedPercent: "",
    winProfitLoss: "",
    placedProfitLoss: "",
    AE: "",
    IV: ""
  };

  onChange = (source, value) => {
    switch (source) {
      case "Runs":
        this.setState({ runs: parseInt(value) });
        break;
      case "Wins":
        this.setState({ wins: parseInt(value) });
        break;
      case "Placed":
        this.setState({ placed: parseInt(value) });
        break;
      case "Win%":
        this.setState({ winPercent: parseFloat(value) });
        break;
      case "Place%":
        this.setState({ placedPercent: parseFloat(value) });
        break;
      case "WinPL":
        this.setState({ winProfitLoss: parseFloat(value) });
        break;
      case "PlacedPL":
        this.setState({ placedProfitLoss: parseFloat(value) });
        break;
      case "AE":
        this.setState({ AE: parseFloat(value) });
        break;
      case "IV":
        this.setState({ IV: parseFloat(value) });
        break;
      default:
        break;
    }
  };

  updateClick = () => {
    this.props.onUpdate(this.state);
  };

  render() {
    return (
      <div className="container mb-3 mt-3">
        <RunSelector title="Runs" onChange={this.onChange} />
        <RunSelector title="Wins" onChange={this.onChange} />
        <RunSelector title="Placed" onChange={this.onChange} />
        <PercentSelector title="Win%" onChange={this.onChange} />
        <PercentSelector title="Place%" onChange={this.onChange} />
        <ProfitLossSelector title="WinPL" onChange={this.onChange} />
        <ProfitLossSelector title="PlacedPL" onChange={this.onChange} />
        <AeIvSelector title="AE" onChange={this.onChange} />
        <AeIvSelector title="IV" onChange={this.onChange} />
        <button
          className="btn btn-outline-success mb-3"
          style={{ height: "70px" }}
          onClick={this.updateClick}
        >
          Update
        </button>
      </div>
    );
  }
}

export default ReportSelection;
